.user-info {
  margin-bottom: 30px;

  &__title {
    display: block;
    font-size: 26px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 30px;
  }

  &__select {
    width: 100%;
    background-color: #fff;
    border: 0.5px solid #000;
    border-radius: 14px;
    position: relative;
    margin-bottom: 30px;

    &--active {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      .user-info__dropdown {
        pointer-events: all;
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
      }
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 18px;
  }

  &__price {
    font-weight: 600;
    font-size: 22px;
    letter-spacing: 0.02em;
    margin-right: 10px;
  }

  &__dropdown {
    max-height: 280px;
    position: absolute;
    top: 100%;
    left: -1px;
    right: -1px;
    z-index: 10;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-10%);
    background-color: #ffffff;
    border: 0.5px solid #000000;
    border-radius: 20px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-top: none;
    overflow: hidden;
    overflow-y: scroll;
    // display: none;
  }

  &__option {
    &:nth-child(odd) {
      background-color: #f4f4f4;
    }
  }

  &__value {
    width: 100%;
    display: inline-block;
    font-size: 15px;
    letter-spacing: 0.02em;
    padding: 18px;
    border: none;
    background-color: transparent;
    color: #171717;
    text-align: left;
  }

  &__btn {
    width: 15px;
    height: 15px;
    border: none;
    border-bottom: 2px solid;
    border-left: 2px solid;
    border-color: #000;
    background-color: transparent;
    transform: rotate(-45deg);
  }

  &__list {
    flex-direction: column;
    display: flex;
    margin-bottom: 16px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__text {
    font-size: 10px;
    text-align: center;
  }
}
