.sr-only {
  display: none;
}

.user-field {
  width: 100%;
  border: none;
  background-color: #f4f4f4;
  border-radius: 20px;
  padding: 18px;
  font-size: 18px;
  border: 2px solid transparent;
  text-align: center;

  &::placeholder {
    color: #000;
  }

  &--error {
    border-color: red;
  }
}
